// get cat image
const img = document.querySelector(".image");
const getImageData = () => {
  axios
    .get("https://api.thecatapi.com/v1/images/search?size=full")
    .then(async (response) => {
      const dataURL = response.data[0].url;
      console.log(`GET users`, dataURL);
      img.src = dataURL;
    })
    .catch((error) => console.error(error));
};

getImageData();

